.spinner {
  width: 20px;
  height: 20px;
  position: relative;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
}

  .spinnerPiece {
    box-sizing: border-box;
    height: 8%;
    width: 24%;
    border-radius: 25%;
    background-color: rgb(143, 143, 143);
    position: absolute;
    animation: spinner_spin 1.2s linear infinite;
  }

  .spinner .spinnerPiece:nth-child(1) {
    transform: rotate(.0001deg) translate(146%);
    animation-delay: -1.2s;
  }
  .spinner .spinnerPiece:nth-child(2) {
    animation-delay: -1.1s;
    transform: rotate(30deg) translate(146%);
  }
  .spinner .spinnerPiece:nth-child(3) {
    animation-delay: -1s;
    transform: rotate(60deg) translate(146%);
  }
  .spinner .spinnerPiece:nth-child(4) {
    animation-delay: -.9s;
    transform: rotate(90deg) translate(146%);
  }
  .spinner .spinnerPiece:nth-child(5) {
    animation-delay: -.8s;
    transform: rotate(120deg) translate(146%);
  }
  .spinner .spinnerPiece:nth-child(6) {
    animation-delay: -.7s;
    transform: rotate(150deg) translate(146%);
  }
  .spinner .spinnerPiece:nth-child(7) {
    animation-delay: -.6s;
    transform: rotate(180deg) translate(146%);
  }
  .spinner .spinnerPiece:nth-child(8) {
    animation-delay: -.5s;
    transform: rotate(210deg) translate(146%);
  }
  .spinner .spinnerPiece:nth-child(9) {
    animation-delay: -.4s;
    transform: rotate(240deg) translate(146%);
  }
  .spinner .spinnerPiece:nth-child(10) {
    animation-delay: -.3s;
    transform: rotate(270deg) translate(146%)
  }
  .spinner .spinnerPiece:nth-child(11) {
    animation-delay: -.2s;
    transform: rotate(300deg) translate(146%)
  }
  .spinner .spinnerPiece:nth-child(12) {
    animation-delay: -.1s;
    transform: rotate(330deg) translate(146%)
  }
  
  @keyframes spinner_spin {
    0% {
      opacity: 1;
    }
    
    100% {
        opacity: .15;
    }
  }

 