button.buttonOne {
  background-color: white;
  border-radius: 6px;
  color: #0A0A0A;
  height: 32px;
  padding: 0 12px;
  font-family: Geist;
  font-weight: 500;
  font-size: 14px;
  font-synthesis-small-caps: auto;
  font-synthesis-style: auto;
  font-synthesis-weight: none;
  text-rendering: optimizelegibility;
  text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  opacity: 1;
  display: flex;
  align-items: center;
  gap: 8px;

  transition: all 250ms;
  border: solid 1px transparent;
}

button.buttonOne:not(:disabled):hover {
  cursor: pointer;
  opacity: 0.85;
}

button.buttonOne:not(:disabled):active {
  cursor: pointer;
  opacity: 0.75;
}


button.buttonOne:disabled  {
  color: #8F8F8F;
  background-color: #191919;
  border: solid 1px #2E2E2E;
  /* pointer-events: none; */
}

button.buttonOne:disabled:hover {
  cursor: not-allowed;
}