.contentCard {
  padding: 32px;
  background-color: rgb(10, 10, 10);
  border-radius: 5px;
  border: solid 1px #333333;
  display: flex;
  flex-direction: column;
}

  .contentCardHeader {
    color: rgb(237, 237, 237);
    font-size: 24px;
    font-weight: 600;
    letter-spacing: -0.47px;
    line-height: 36px;
  }

  .contentCardHeader2 {
    color: rgb(237, 237, 237);
    font-size: 20px;
    font-weight: 600;
    letter-spacing: -0.33px;
    line-height: 30px;
  }

  .contentCardText {
    color: rgb(153, 153, 153);
    font-size: 14px;
    font-weight: 400;
    letter-spacing: -0.09px;
    line-height: 21px;
    margin: 24px 0;
  }

  .contentCardText2 {
    color: rgb(237, 237, 237);
    font-size: 14px;
    font-weight: 400;
    letter-spacing: -0.09px;
    line-height: 21px;
    margin: 12px 0;
    white-space: pre-wrap;
  }

  .contentCardLabel {
    color: rgb(161, 161, 161);
    font-size: 13px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 8px;
  }

  .contentBadge {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20px;
    padding: 0 6px;
    border-radius: 9999px;
    background-color: oklch(0.5761 0.2321 258.23);
    font-size: 11px;
    font-weight: 500;
    line-height: 24px;
    color: rgb(255, 255, 255);
  }

  .contentCardSpacer {
    border-top: solid 1px #333333;
  }

  .contentSwitch {
    padding: 4px;
    border-radius: 6px;
    border: solid 1px rgb(31, 31, 31);
    background-color: rgb(10, 10, 10);
    display: flex;
    width: fit-content;
  }

    .contentSwitchOption {
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 12px;
      font-size: 14px;
      font-weight: 500;
      color: rgb(161, 161, 161);
      border-radius: 2px;
      background-color: transparent;

      transition: all 250ms;
    }

    .contentSwitchOption:hover {
      cursor: pointer;
      color: rgb(237, 237, 237);
    }

    .contentSwitchOption[data-selected] {
      color: rgb(237, 237, 237);
      background-color: rgb(26, 26, 26);
    }






  /* Results Card Styles */
  .resultsPreviews {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .resultPreviewCard {
    background-color: rgb(17, 17, 17);
    border-radius: 4px;
    border: solid 1px rgb(51, 51, 51);
    display: flex;
    flex-direction: column;
    max-height: 82px;
    overflow: hidden;
  }
  .resultPreviewCard[data-opened] {
    max-height: 100%;
  }

  .resultPreviewCardHeaderArea {
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 0 24px;
    min-height: 82px;
  }

  .resultPreviewCardHeaderArea:hover {
    cursor: pointer;
  }

  .resultPreviewCardContentArea {
    padding: 0 24px;
    padding-bottom: 24px;
  }

    .chevron {
      width: 16px;
      height: 16px;

      transition: all 250ms;
    }

    .chevron path {
      fill: white;
    }

    .resultPreviewVisualizationContainer {
      padding: 24px;
      border-radius: 6px;
      border: solid 1px rgb(31, 31, 31);
      background-color: rgb(10, 10, 10);
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .resultPreviewImage {
      max-width: 75%;
      border-radius: 6px;
      border: solid 1px rgb(31, 31, 31);
    }

    .resultPreviewDetailsContainer {
      padding: 24px;
      border-radius: 6px;
      border: solid 1px rgb(31, 31, 31);
      background-color: rgb(10, 10, 10);
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    .tableRow {
      height: 40px;
      padding: 0 8px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    .tableRowText {
      font-size: 14px;
      line-height: 20px;
      font-weight: 400;
      color: rgb(237, 237, 237);
    }

    .tableSpacer {
      border-top: solid 1px rgb(31, 31, 31);
    }






  /* Upload Card Styles */
  .uploadPreviews {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .uploadPreviewCard {
    border-radius: 6px;
    padding: 16px;
    border: solid 1px rgb(51, 51, 51);
    background-color: rgb(17, 17, 17);
    display: flex;
    align-items: center;
    gap: 10px;
  }

    .uploadPreviewCardText {
      color: rgb(237, 237, 237);
      font-size: 13px;
      line-height: 24px;
    }

  .imgIcon {
    height: 16px;
    width: 16px;
  }

  .imgIcon path {
    fill: white;
  }

  .crossIcon {
    height: 16px;
    margin-left: auto;
  }

  .uploadPreviewCard:not([data-disabled]) .crossIcon:hover {
    cursor: pointer;
  }

  .uploadPreviewCard[data-disabled] .crossIcon:hover {
    cursor: not-allowed;
  }

  .crossIcon path {
    fill: #A1A1A1;

    transition: all 250ms;
  }

  .uploadPreviewCard:not([data-disabled]) .crossIcon:hover path {
    fill: white;
  }




