.dragAndDrop {
  border: dashed 1px rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  padding: 24px;
  color: #a1a1a1;
  font-weight: 400;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;

  transition: all 250ms;
}

  .dragAndDrop:not([data-disabled]):hover path {
    fill: rgb(237, 237, 237);
  }

  .dragAndDropIcon {
    height: 16px;
    width: 16px;
  }
  .dragAndDropIcon path {
    fill: #888;

    transition: all 250ms;
  }

.dragAndDrop:not([data-disabled]):hover {
  cursor: copy;
  border: dashed 1px rgb(237, 237, 237);
  color: rgb(237, 237, 237);
}

.dragAndDrop[data-disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}
