.container {
  min-height: 100%;
  box-sizing: border-box;
  /* border: solid green 1px; */
  position: relative;
}

  .navigation {
    height: 64px;
    background-color: rgb(10, 10, 10);
  }

  .main {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 24px;
    position: relative;
    z-index: 1;
  }

    .content-container {
      display: flex;
      gap: 72px;
      /* border: solid red 1px; */
    }


  .background {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 381px;
    border-top: solid 1px #333333;
    background-color: #111111;
    z-index: 0;
  }