.info {
  min-width: 288px;
  padding-top: 160px;
}

  .infoHeader {
    color: rgb(161, 161, 161);
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    line-height: 20px;
  }

  .spacer {
    border-top: solid 1px #333333;
    margin: 22px 0; 
  }


  .flowTextContainer {
    display: flex;
    align-items: center;
    gap: 9px;
  }

    .flowTextIndicator {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      box-sizing: border-box;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 5;

      transition: all 250ms;
    }
    .flowTextContainer[data-active="true"] .flowTextIndicator {
      background-color: white;
    }
    .flowTextContainer[data-active="false"] .flowTextIndicator {
      background-color: rgb(51, 51, 51);
    }

    .flowText {
      font-size: 14px;
      font-weight: 500;

      transition: all 250ms;
    }

    .flowTextContainer[data-active="true"] .flowText {
      color: rgb(255, 255, 255);
    }

    .flowTextContainer[data-active="false"] .flowText {
      color: rgb(68, 68, 68);
    }



    .flowTextLine {
      position: absolute;
      height: 35px;
      bottom: 100%;
      width: 1px;
      z-index: 1;
      background-color: rgb(51, 51, 51);

      transition: all 250ms;
    }

